// Toggle global options
$enable-gradients: false;
$enable-shadows: true;

// Customize some defaults
$body-bg: rgb(240, 240, 240);
$text-color: rgb(20, 20, 20);

$primary: #177abd;
$primary-light: #66baf2;
$primary-dark: #177abd; //#0d4970;

$primary-gradient: #177abd;
$primary-gradient-state: #66baf2;

$secondary: #de0dd8;
$success: #005e14;
$success-light: #5aaf49;
$info: #f6d0b1;
$warning: #f7b05b;
$dark: #272727;
$light: #e9eaeb;
$danger: #a4001d;
$black: #000;

$text-light: #f2f2f2;
$text-dark: $text-color;

$bg-nav: #e9eaeb !default; //$gray-200
$bg-dark-blue: #3a416f;

$link-color: $text-color;
$link-hover-color: $primary-dark;

$font-family-sans-serif: "Argentum Sans", Helvetica, Arial, sans-serif !default;
$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-roboto-slab: "Argentum Sans", sans-serif !default;

$border-width: 0px;
